.grid__row {
  position: relative;
  width: 100%;
  min-height: 1px; }

.grid__container, .home__about-container {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  padding: 0 20px;
  margin-left: auto;
  margin-right: auto;
  max-width: 1200px;
  width: 100%; }

.grid__container-fluid {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  margin-left: auto;
  margin-right: auto;
  max-width: 100%;
  width: 100%; }

.grid__col-1-1, .grid__col-1-2, .grid__col-1-3 {
  position: relative; }

.grid__col-1-1 {
  width: 100%; }

.grid__col-1-2 {
  width: 50%; }

.grid__col-1-2:nth-of-type(odd) {
  padding-right: 10px; }

.grid__col-1-2:nth-of-type(even) {
  padding-left: 10px; }

.grid__col-1-3 {
  width: 33.33333%; }

.grid__col-1-3:nth-of-type(3n-2) {
  padding-right: 13.33333px; }

