/* A s p e c t   r a t i o */
%box-aspect-ratio {
    position: relative;
    width: 100%;
    background-color: whitesmoke;
}

%imagen-aspect-ratio {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    user-select: none;
    object-fit: cover;
}

%flex-center, .flex-center {
     display: flex !important;
     align-items: center;
     justify-content: center;
}

%flex-star-center, .flex-star-center {
     display: flex;
     flex-direction: row;
     align-items: flex-start;
     justify-content: center;
     @include under(break) {
          flex-direction: column ;
     }
}

%flex-row-star, .flex-row-star {
     display: flex;
     flex-direction: row;
     justify-content: start;
}

%flex-row-between, .flex-row-between {
     display: flex;
     flex-direction: row;
     justify-content: space-between;
}

%flex-row-center, .flex-row-center {
     display: flex ;
     flex-direction: row ;
     justify-content: center ;
}


%flex-column, .flex-column {
     display: flex;
     flex-direction: column;
}
